.display {
    text-align: right;
    padding: 5px 20px;
    margin: 20px;
    width: 100%;
    height: 60px;
    background-color: var(--display-color);
    border: 3px solid var(--inner-borders);
    border-radius: 5px;
    overflow: hidden;
    font-size: 1rem;
    font-weight: 400;
}
